.descriptionBlock {
    padding-left: 10px;
}

.name {
    color: #fdfdfd;
    font-size: 20px;
}
.avatar {
    max-width: 230px;
}
.avatarWrapper {
    display: inline-block;
    position: relative;
}
.iconWrapper {
    position: absolute;
    top: 10px;
    right: 10px;
    cursor: pointer;
}
.inputFileIcon {
    max-width: 30px;
}
.inputFile {
    opacity: 0;
    visibility: hidden;
    position: absolute;
}


